import React, { useCallback, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { Box, FormHelperText, makeStyles, Theme, Typography } from '@material-ui/core';

const height: number = 256;
const useStyles = makeStyles((theme: Theme) => ({
  root: {
    marginTop: theme.spacing(2),
    composes: 'flexCenter center stretch flex1',
    borderRadius: 3,
    background: '#f5f5f5 right 20px center no-repeat',
    cursor: 'pointer',
    padding: 10,
    backgroundColor: '#ffffff',
    border: '1px solid #e4e5e6',
    minHeight: height,
    position: 'relative',
    transition: 'background-color ease .5s , border-color ease .5s',
    width: '100%',
    '&:hover': {
      backgroundColor: '#e1f5fe',
    	border: '1px dashed #4285f4',
    },
  },
  imageContainer: {
    display: 'flex',
    width: 200,
    height,
    border: '1px solid #e4e5e6',
  },
  img: {
    width: 180,
    marginBottom: theme.spacing(2),
  },
  label: {
    color: theme.palette.primary.main,
  },
  thumbnail: {
    width: '100%',
    objectFit: 'contain',
  },
}));

type Props = {
	mimeType: string;
	maxFiles?: number;
	input?: any;
  meta: { error: string; touched: boolean };
};

const DropFileUploader = ({ 
  input: { name, onChange }, 
  meta: { error, touched },
  mimeType = 'image/*', 
  maxFiles = 1, 
}: Props) => {

  // styles
  const classes = useStyles();

  // state
  // const [currentFile, setCurrentFile] = useState<any>(null);
  const [currentFiles, setCurrentFiles] = useState<any[]>([]);

  const onDrop = useCallback((acceptedFiles) => {
    const files: any[] = [];
    // Do something with the files
    acceptedFiles.forEach((file: any) => files.push(file));
    setCurrentFiles([...files]);

    if (maxFiles === 1) {
      onChange(files[0]);
      return;
    }
    onChange([...files]);
  }, [onChange]);

  // dropzone props
  const {
    getRootProps,
    getInputProps,
    isDragActive,
  } = useDropzone({ 
    accept: mimeType,
    onDrop, 
    maxFiles,
  });

  return (
		<Box width="100%">
      <div 
        {...getRootProps()} 
        className={classes.root}
      >
        {/* -------------------- input -------------------- */}
        <input {...getInputProps({ multiple: !!(maxFiles > 1) })} name={name} />

        {(!currentFiles || currentFiles.length <= 0) && !isDragActive
        /* -------------------- placeholder -------------------- */
          ? (
            <>
              <Box display="flex">
                <img
                  alt="" 
                  className={classes.img} 
                  src="data:image/png;base64," 
                />
                {maxFiles > 1 && (
                  <img
                    alt="" 
                    className={classes.img} 
                    src="data:image/png;base64," 
                  />
                )}            
              </Box>
              <Typography variant="subtitle2" gutterBottom className={classes.label}>Add images</Typography>
              <Typography variant="subtitle2" gutterBottom>Drag and drop to upload</Typography>
            </>
          ) 

        /* -------------------- selected files -------------------- */
          : (
            <Box display="flex" flex={1} alignSelf="stretch">
              {currentFiles && currentFiles.length >= 1 && currentFiles.map((currentFile: any, index: number) => (
                <div
                className={classes.imageContainer}
                  key={index}
                >
                  <img alt="" src={URL.createObjectURL(currentFile)} className={classes.thumbnail} />
                </div>
              ))}
            </Box>
          )}
      </div>
      {/* --------------- Error ------------------- */}
      {touched && error && (
        <FormHelperText error>
          { error }
        </FormHelperText>
      )}
  </Box>
  );
};

export default DropFileUploader;